import React from 'react';
import PropTypes from 'prop-types';
import 'tui-chart/dist/tui-chart-polyfill.min.js';
import 'tui-chart/dist/tui-chart.css';

class Chart extends React.Component {
  componentDidMount() {
    this.createChart();
  }

  render() {
    return (
      <div className="tui-section-demo-chart">
        <div>
          <div style={{ display: 'inline-block' }} id="chart" />
          <span> </span>
        </div>
      </div>
    );
  }

  createChart() {
    const container = document.getElementById('chart');
    const { data, options, theme, factoryFunc } = this;
    const { application } = this.props;
    if (theme) {
      application.registerTheme('custom', theme);
      options.theme = 'custom';
    }

    application[factoryFunc](container, data, options, theme);
  }
}

Chart.propTypes = {
  application: PropTypes.object.isRequired,
};

export default Chart;
