import Editor from './Editor';

import 'tui-chart/dist/tui-chart.css';

class EditorChart extends Editor {
  createEditor() {
    var content = [
      '```chart',
      ',Budget,Income,Expenses,Debt',
      'June,5000,8000,4000,6000',
      'July,3000,1000,4000,3000',
      'Aug,5000,7000,6000,3000',
      'Sep,7000,2000,3000,1000',
      'Oct,6000,5000,4000,2000',
      'Nov,4000,3000,5000,4000',
      '',
      'type: column',
      'title: Monthly Revenue',
      'width: 465',
      'x.title: Amount',
      'y.title: Month',
      'y.suffix: $',
      'legend.visible: false',
      '```',
    ].join('\n');
    const TuiEditor = this.props.application;

    return new TuiEditor({
      el: document.getElementById('editor'),
      previewStyle: 'vertical',
      initialEditType: 'markdown',
      initialValue: content,
      descriptionStyle: 'vertical',
      height: '574px',
      exts: ['scrollSync', 'colorSyntax', 'chart', 'uml', 'table'],
    });
  }
}

export default EditorChart;
