export type PackageName =
  | 'tui-chart'
  | 'tui-editor'
  | 'tui-grid'
  | 'tui-calendar'
  | 'tui-image-editor';

const componentMap = {
  'tui-chart': [
    BarChart,
    ColumnChart,
    LineChart,
    AreaChart,
    PieChart,
    BubbleChart,
    ScatterChart,
    MapChart,
    HeatmapChart,
    TreemapChart,
    RadialChart,
    BoxplotChart,
  ],
  'tui-editor': [EditorPreview, EditorAll, EditorChart, EditorUML],
  'tui-grid': [GridPreview, GridTheme, GridTheme, GridTheme],
  'tui-calendar': [
    CalendarMonthly,
    CalendarWeekly,
    CalendarDaily,
    Calendar2Weeks,
    Calendar3Weeks,
    CalendarNarrowerWeekends,
    CalendarHideWeekends,
    CalendarTaskView,
    CalendarOnlyTaskView,
    CalendarTheme,
    CalendarTimezone,
  ],
  'tui-image-editor': [
    ImageEditorBlack,
    ImageEditorWhite,
    ImageEditorBlackLeft,
    ImageEditorWhiteRight,
    ImageEditorBlackTop,
  ],
};

export function getDemoComponent(packageName: PackageName): React.ElementType[] {
  return componentMap[packageName];
}
