/* eslint-disable require-jsdoc */
import Chance from 'chance';
import moment from 'moment';

const chance = new Chance();
const SCHEDULE_CATEGORY = ['milestone', 'task'];

let schedules = [];

function ScheduleInfo() {
  this.id = null;
  this.calendarId = null;

  this.title = null;
  this.isAllday = false;
  this.start = null;
  this.end = null;
  this.category = '';
  this.dueDateClass = '';

  this.color = null;
  this.bgColor = null;
  this.dragBgColor = null;
  this.borderColor = null;
  this.customStyle = '';

  this.isFocused = false;
  this.isPending = false;
  this.isVisible = true;
  this.isReadOnly = false;

  this.raw = {
    memo: '',
    hasToOrCc: false,
    hasRecurrenceRule: false,
    location: null,
    class: 'public', // or 'private'
    creator: {
      name: '',
      avatar: '',
      company: '',
      email: '',
      phone: '',
    },
  };
}

function generateTime(schedule, renderStart, renderEnd, options) {
  let endDate = moment(renderEnd.getTime());
  const startDate = moment(renderStart.getTime());
  const diffDate = endDate.diff(startDate, 'days');
  const { taskView, scheduleView } = options;

  schedule.isAllday = chance.bool({ likelihood: 20 });
  if (schedule.isAllday) {
    schedule.category = 'allday';
  } else if (taskView && !scheduleView) {
    schedule.category = SCHEDULE_CATEGORY[chance.integer({ min: 0, max: 1 })];
    if (schedule.category === SCHEDULE_CATEGORY[1]) {
      schedule.dueDateClass = 'morning';
    }
  } else if (chance.bool({ likelihood: 30 })) {
    schedule.category = SCHEDULE_CATEGORY[chance.integer({ min: 0, max: 1 })];
    if (schedule.category === SCHEDULE_CATEGORY[1]) {
      schedule.dueDateClass = 'morning';
    }
  } else {
    schedule.category = 'time';
  }

  startDate.add(chance.integer({ min: 0, max: diffDate }), 'days');
  startDate.hours(chance.integer({ min: 0, max: 23 }));
  startDate.minutes(chance.bool() ? 0 : 30);
  schedule.start = startDate.toDate();

  endDate = moment(startDate);
  if (schedule.isAllday) {
    endDate.add(chance.integer({ min: 0, max: 2 }), 'days');
  }

  schedule.end = endDate.add(chance.integer({ min: 1, max: 4 }), 'hour').toDate();
}

function generateRandomSchedule(calendar, renderStart, renderEnd, options) {
  const schedule = new ScheduleInfo();

  schedule.id = chance.guid();
  schedule.calendarId = calendar.id;

  schedule.title = chance.sentence({ words: 3 });
  schedule.isReadOnly = chance.bool({ likelihood: 20 });
  generateTime(schedule, renderStart, renderEnd, options);

  schedule.isPrivate = chance.bool({ likelihood: 10 });
  schedule.location = chance.address();
  schedule.attendees = chance.bool({ likelihood: 70 }) ? ['anyone'] : [];
  schedule.recurrenceRule = chance.bool({ likelihood: 20 });

  schedule.color = calendar.color;
  schedule.bgColor = calendar.bgColor;
  schedule.dragBgColor = calendar.dragBgColor;
  schedule.borderColor = calendar.borderColor;

  if (schedule.category === 'milestone') {
    schedule.color = schedule.bgColor;
    schedule.bgColor = 'transparent';
    schedule.dragBgColor = 'transparent';
    schedule.borderColor = 'transparent';
  }

  schedule.raw.memo = chance.sentence();
  schedule.raw.creator.name = chance.name();
  schedule.raw.creator.avatar = chance.avatar();
  schedule.raw.creator.company = chance.company();
  schedule.raw.creator.email = chance.email();
  schedule.raw.creator.phone = chance.phone();

  schedules.push(schedule);
}

export function generateSchedule(calendars, viewName, renderStart, renderEnd, options) {
  schedules = [];
  calendars.forEach((calendar) => {
    let i = 0,
      length = 5;

    if (viewName === 'month') {
      length = 6;
    } else if (viewName === 'day') {
      length = 4;
    }
    for (; i < length; i += 1) {
      generateRandomSchedule(calendar, renderStart, renderEnd, options);
    }
  });

  return schedules;
}
