import Calendar from './Calendar';

class CalendarTimezone extends Calendar {
  constructor() {
    super();

    this.options.defaultView = 'week';
    this.options.taskView = false;
    this.options.timezones = [
      {
        timezoneOffset: 540,
        tooltip: 'Seoul',
      },
      {
        timezoneOffset: -420,
        tooltip: 'Los Angeles',
      },
    ];
    this.options.theme = {
      'week.daygridLeft.width': '56px',
      'week.timegridLeft.width': '56px',
    };
  }
}

export default CalendarTimezone;
