import Editor from './Editor';

import 'tui-chart/dist/tui-chart.css';

class EditorUML extends Editor {
  createEditor() {
    var content = [
      '```uml',
      'partition Conductor {',
      '  (*) --> "Climbs on Platform"',
      '  --> === S1 ===',
      '  --> Bows',
      '}',
      '',
      'partition Audience #LightSkyBlue {',
      '  === S1 === --> Applauds',
      '}',
      '',
      'partition Conductor {',
      '  Bows --> === S2 ===',
      '  --> WavesArmes',
      '  Applauds --> === S2 ===',
      '}',
      '',
      'partition Orchestra #CCCCEE {',
      '  WavesArmes --> Introduction',
      '  --> "Play music"',
      '}',
      '```',
    ].join('\n');
    const TuiEditor = this.props.application;

    return new TuiEditor({
      el: document.getElementById('editor'),
      previewStyle: 'vertical',
      initialEditType: 'markdown',
      initialValue: content,
      descriptionStyle: 'vertical',
      height: '574px',
      exts: ['scrollSync', 'colorSyntax', 'chart', 'uml', 'table'],
    });
  }
}

export default EditorUML;
